import React, { useState, useEffect, useRef, useCallback, useMemo } from "react";
import "./ProjectAI.css";

// ============================================
// SMART CHIPS
// ============================================
const getChipsForTab = (tabName, projectName) => {
  const base = {
    "README.md": [
      `Summarize ${projectName}`,
      "What's unique about this?",
      "Biggest challenge?",
    ],
    "PROBLEM.md": [
      "Why was this important?",
      "Existing solutions?",
      "User pain point?",
    ],
    "SOLUTION.md": [
      "Explain the architecture",
      "Why this tech stack?",
      "How does data flow?",
    ],
    "tech-stack.json": [
      "Why these tools?",
      "Any trade-offs?",
      "What would you change?",
    ],
    "impact.md": [
      "Key metrics?",
      "How was success measured?",
      "What's next?",
    ],
  };
  return base[tabName] || base["README.md"];
};

// ============================================
// NARRATOR SCRIPT
// ============================================
const getNarratorScript = (project) => {
  const d = project?.details || {};
  return [
    { title: "Intro", text: `${project.name}. ${project.description}`, tab: "README.md", duration: 5000 },
    { title: "Problem", text: d.story?.problem || d.problem || "The problem this solves.", tab: "PROBLEM.md", duration: 6000 },
    { title: "Solution", text: d.story?.solution || d.solution || "Here's the solution.", tab: "SOLUTION.md", duration: 6000 },
    { title: "Tech", text: `Built with ${project.languages?.map(l => l.name).join(", ") || "modern tech"}.`, tab: "tech-stack.json", duration: 4000 },
    { title: "Impact", text: d.impact?.[0] || "Significant impact achieved.", tab: "impact.md", duration: 4000 },
  ];
};

// ============================================
// MAIN COMPONENT
// ============================================
const ProjectAI = ({
  project,
  activeTab,
  scrollProgress,
  onTabChange,
  onOpenSiri,
  isVisible,
}) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [hasHinted, setHasHinted] = useState(false);
  const [showHint, setShowHint] = useState(false);
  const [hintDismissed, setHintDismissed] = useState(false);
  const [activeMode, setActiveMode] = useState("chips");
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const [narratorPlaying, setNarratorPlaying] = useState(false);
  const [narratorStep, setNarratorStep] = useState(0);

  //  Refs to avoid dependency loops
  const onTabChangeRef = useRef(onTabChange);
  const onOpenSiriRef = useRef(onOpenSiri);
  const hintTimer = useRef(null);
  const narratorTimer = useRef(null);
  const speechTimeout = useRef(null);

  const chips = useMemo(
    () => getChipsForTab(activeTab, project?.name),
    [activeTab, project?.name]
  );
  const narratorScript = useMemo(
    () => getNarratorScript(project),
    [project]
  );

  // ---- Keep refs updated ----
  useEffect(() => { onTabChangeRef.current = onTabChange; }, [onTabChange]);
  useEffect(() => { onOpenSiriRef.current = onOpenSiri; }, [onOpenSiri]);

  // ---- Mobile detection ----
  useEffect(() => {
    const check = () => setIsMobile(window.innerWidth <= 768);
    window.addEventListener("resize", check);
    return () => window.removeEventListener("resize", check);
  }, []);

  // ============================================
  //  SINGLE NARRATOR EFFECT  No duplicates
  // ============================================
  useEffect(() => {
    if (!narratorPlaying) return;

    const step = narratorScript[narratorStep];
    if (!step) {
      setNarratorPlaying(false);
      return;
    }

    console.log(` Narrator step ${narratorStep}: ${step.title}`); // Debug

    // Switch tab
    if (onTabChangeRef.current) {
      onTabChangeRef.current(step.tab);
    }

    //  Speech with delay
    const synth = window.speechSynthesis;
    if (synth) {
      synth.cancel();

      speechTimeout.current = setTimeout(() => {
        // Check voices are loaded
        let voices = synth.getVoices();
        
        // Chrome sometimes needs a moment to load voices
        if (voices.length === 0) {
          synth.onvoiceschanged = () => {
            voices = synth.getVoices();
          };
          // Try again after a beat
          setTimeout(() => {
            voices = synth.getVoices();
            speakText(synth, voices, step.text);
          }, 300);
        } else {
          speakText(synth, voices, step.text);
        }
      }, 200);
    }

    // Advance to next step
    narratorTimer.current = setTimeout(() => {
      if (narratorStep < narratorScript.length - 1) {
        setNarratorStep(p => p + 1);
      } else {
        setNarratorPlaying(false);
      }
    }, step.duration);

    return () => {
      clearTimeout(narratorTimer.current);
      clearTimeout(speechTimeout.current);
      //  Do NOT cancel speech here
    };
  }, [narratorStep, narratorPlaying, narratorScript]);
  //  No onTabChange  uses ref instead

  //  Helper function for speech
  const speakText = (synth, voices, text) => {
    const u = new SpeechSynthesisUtterance(text);
    u.rate = 0.9;
    u.pitch = 1.05;
    u.volume = 1;

    const voice =
      voices.find(v => v.name.includes("Google US English")) ||
      voices.find(v => v.name.includes("Samantha")) ||
      voices.find(v => v.name.includes("Daniel")) ||
      voices.find(v => v.lang === "en-US") ||
      voices.find(v => v.lang.startsWith("en")) ||
      voices[0];

    if (voice) u.voice = voice;

    console.log(` Speaking: "${text.slice(0, 50)}..." with voice: ${voice?.name}`);
    synth.speak(u);
  };

  // ---- Hint (shows once after 30s) ----
  useEffect(() => {
    if (hasHinted || hintDismissed) return;
    hintTimer.current = setTimeout(() => {
      setShowHint(true);
      setHasHinted(true);
      setTimeout(() => setShowHint(false), 6000);
    }, 30000);
    return () => clearTimeout(hintTimer.current);
  }, [hasHinted, hintDismissed]);

  // ---- Handlers ----
  const dismissHint = useCallback(() => {
    setShowHint(false);
    setHintDismissed(true);
    clearTimeout(hintTimer.current);
  }, []);

  const toggleExpand = useCallback(() => {
    setIsExpanded(prev => !prev);
    dismissHint();
  }, [dismissHint]);

  const closePanel = useCallback(() => {
    setIsExpanded(false);
    // Only stop narrator if panel closes, NOT speech
  }, []);

  const handleChipClick = useCallback((chip) => {
    closePanel();
    if (onOpenSiriRef.current) onOpenSiriRef.current(chip);
  }, [closePanel]);

  const startNarrator = useCallback(() => {
    setNarratorStep(0);
    setNarratorPlaying(true);
    //  Auto-close panel so content is visible
    setIsExpanded(false);
  }, []);

  const stopNarrator = useCallback(() => {
    setNarratorPlaying(false);
    setNarratorStep(0);
    clearTimeout(narratorTimer.current);
    clearTimeout(speechTimeout.current);
    //  Only cancel speech when user explicitly stops
    window.speechSynthesis?.cancel();
  }, []);

  // ---- Escape to close ----
  useEffect(() => {
    const handleKey = (e) => {
      if (e.key === "Escape") {
        if (narratorPlaying) stopNarrator();
        else closePanel();
      }
    };
    window.addEventListener("keydown", handleKey);
    return () => window.removeEventListener("keydown", handleKey);
  }, [closePanel, stopNarrator, narratorPlaying]);

  if (!isVisible) return null;

  // ============================================
  // RENDER
  // ============================================
  return (
    <div className="project-ai-wrapper">

      {/* ---- Subtle hint ---- */}
      {showHint && !isExpanded && !narratorPlaying && (
        <div className="ai-hint" onClick={dismissHint} role="status">
          <span></span>
          <span className="ai-hint-text">I can explain this project</span>
          <button
            className="ai-hint-dismiss"
            onClick={(e) => { e.stopPropagation(); dismissHint(); }}
            aria-label="Dismiss"
          >
            
          </button>
        </div>
      )}

      {/* ---- Narrator floating bar (when playing but panel closed) ---- */}
      {narratorPlaying && !isExpanded && (
        <div className="ai-narrator-bar">
          <div className="narrator-mini-wave">
            {[...Array(4)].map((_, i) => (
              <span key={i} className="mini-wave-bar" />
            ))}
          </div>
          <span className="narrator-bar-title">
            {narratorScript[narratorStep]?.title}
          </span>
          <div className="narrator-bar-progress">
            {narratorScript.map((_, i) => (
              <span
                key={i}
                className={`progress-dot ${
                  i === narratorStep ? "active" :
                  i < narratorStep ? "done" : ""
                }`}
              />
            ))}
          </div>
          <button
            className="narrator-bar-stop"
            onClick={stopNarrator}
            aria-label="Stop narration"
          >
            
          </button>
        </div>
      )}

      {/* ---- AI Pill Button ---- */}
      {!narratorPlaying && (
        <button
          className={`ai-pill ${isExpanded ? "active" : ""}`}
          onClick={toggleExpand}
          aria-label={isExpanded ? "Close AI" : "Open AI"}
          aria-expanded={isExpanded}
        >
          <span className="ai-pill-icon"></span>
          {!isMobile && <span className="ai-pill-label">AI</span>}
        </button>
      )}

      {/* ---- Expanded Panel ---- */}
      {isExpanded && (
        <>
          {/*  Backdrop  NO blur on mobile, just catches clicks */}
          <div
            className={`ai-backdrop ${isMobile ? "mobile" : ""}`}
            onClick={closePanel}
          />

          <div className={`ai-panel ${isMobile ? "mobile" : ""}`}>
            <div className="ai-panel-header">
              <div className="ai-panel-title">
                <span></span>
                <span>JARVIS</span>
                <span className="ai-panel-context">{activeTab}</span>
              </div>
              <button className="ai-panel-close" onClick={closePanel} aria-label="Close">
                
              </button>
            </div>

            <div className="ai-panel-modes">
              <button
                className={activeMode === "chips" ? "active" : ""}
                onClick={() => setActiveMode("chips")}
              >
                 Ask
              </button>
              <button
                className={activeMode === "narrator" ? "active" : ""}
                onClick={() => setActiveMode("narrator")}
              >
                 Present
              </button>
              <button
                onClick={() => {
                  closePanel();
                  if (onOpenSiriRef.current) onOpenSiriRef.current();
                }}
              >
                 Chat
              </button>
            </div>

            <div className="ai-panel-body">
              {activeMode === "chips" && (
                <div className="ai-chips-section">
                  <div className="ai-chips-list">
                    {chips.map((chip, i) => (
                      <button
                        key={chip}
                        className="ai-chip"
                        onClick={() => handleChipClick(chip)}
                        style={{ animationDelay: `${i * 0.06}s` }}
                      >
                        {chip}
                      </button>
                    ))}
                  </div>
                  <button
                    className="ai-custom-ask"
                    onClick={() => {
                      closePanel();
                      if (onOpenSiriRef.current) onOpenSiriRef.current();
                    }}
                  >
                     Ask something else...
                  </button>
                </div>
              )}

              {activeMode === "narrator" && (
                <div className="ai-narrator-section">
                  <div className="narrator-steps">
                    {narratorScript.map((step, i) => (
                      <div key={i} className="narrator-step">
                        <div className="narrator-step-dot" />
                        <span>{step.title}</span>
                      </div>
                    ))}
                  </div>
                  <button className="narrator-start-btn" onClick={startNarrator}>
                     Auto-present this project
                  </button>
                  <p className="narrator-note">
                    Panel will close so you can see content while it narrates
                  </p>
                </div>
              )}
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default ProjectAI;